.ck.ck-input-text{border-radius:0}.ck-rounded-corners .ck.ck-input-text,.ck.ck-input-text.ck-rounded-corners{border-radius:2px}.ck.ck-input-text{box-shadow:inset 2px2px3pxhsla(0,0,0,.1),0 0;background:#fff;border:1px solid #c7c7c7;padding:.096em .48em;min-width:18em;min-height:2.3em;transition:box-shadow .2s ease-in-out,border .2s ease-in-out}.ck.ck-input-text:focus{outline:none;border:1px solid #1f89e5;box-shadow:0003pxhsl(207,89%,86%),inset 2px 2px 3px rgba(0,0,0,.1)}.ck.ck-input-text[readonly]{border:1px solid #c7c7c7;background:#f2f2f2;color:#5c5c5c}.ck.ck-input-text[readonly]:focus{box-shadow:0003pxhsla(209,90%,72%,.3),inset 2px 2px 3px rgba(0,0,0,.1)}.ck.ck-input-text.ck-error{border-color:#db3700;animation:ck-text-input-shake .3s ease both}.ck.ck-input-text.ck-error:focus{box-shadow:0003pxhsla(9,100%,56%,.3),inset 2px 2px 3px rgba(0,0,0,.1)}@keyframes ck-text-input-shake{20%{transform:translateX(-2px)}40%{transform:translateX(2px)}60%{transform:translateX(-1px)}80%{transform:translateX(1px)}}